import { request } from 'utils';

const prefix = '/api/categories';

export const getCourses = ({
  label,
  trainTypeId,
  facultyId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Course`,
    method: 'GET',
    params: { label, trainTypeId, facultyId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



