import { request } from 'utils';

const prefix = '/api/categories';

export const getFacultys = ({
  label,
  labelInEnglish,
  trainTypeId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Faculty`,
    method: 'GET',
    params: { label, labelInEnglish, trainTypeId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



