import { request } from 'utils';

const prefix = '/api/students';

export const getClasss = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Class',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getClassById = id =>
  request({
    prefix,
    url: `/Class/${id}`,
    method: 'GET',
  });

export const deleteClass = id =>
  request({
    prefix,
    url: `/Class/${id}`,
    method: 'DELETE',
  });

export const updateClass = ({ id, ...values }) => {
  console.log('value ====>', values);
  return request({
    prefix,
    url: `/Class/${id}`,
    data: values,
    method: 'PUT',
  });
};

export const createClass = value =>
  request({
    prefix,
    url: `/Class`,
    data: value,
    method: 'POST',
  });



