import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Select, Form, Input } from 'antd';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
// import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { getClassById, createClass, updateClass } from 'services/class';

const { Option } = Select;
const initialValueDefault = {
  code: '',
  certType: '',
  trainTypeId: '',
};
// const { Option } = Select;
@Form.create({ name: 'FormClassesPage' })
class FormClassesPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
    dataTrainTypes: [],
    dataFacultys: [],
    dataCourses: [],
    dataStudyFields: [],
  };

  componentDidMount = async () => {
    const { match, form } = this.props;
    this.getdataFrom();
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      const initialValue = await this.getDataById(match.params.id);
      form.setFieldsValue(initialValue);
    } else {
      form.setFieldsValue(initialValueDefault);
    }
  };

  getdataFrom = async () => {
    const dataTrainTypes = await getTrainTypes();
    const dataFacultys = await getFacultys();
    const dataCourses = await getCourses();
    const dataStudyFields = await getStudyFields();
    const dataYear = dataCourses.data.items[0];
    this.setState({
      fromYear: dataYear.fromYear,
      toYear: dataYear.toYear,
      dataTrainTypes: dataTrainTypes.data.items,
      dataFacultys: dataFacultys.data.items,
      dataCourses: dataCourses.data.items,
      dataStudyFields: dataStudyFields.data.items,
    });
  };

  getDataById = async id => {
    const res = await getClassById(id);
    return { ...initialValueDefault, ...res.data };
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createClass(values);
      } else {
        await updateClass({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  changeCourse = course => {
    const { dataCourses } = this.state;
    const dataYear = dataCourses.filter(item => item.id === course);
    this.setState({
      fromYear: dataYear[0].fromYear,
      toYear: dataYear[0].toYear,
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const {
      dataId: zoneId,
      dataTrainTypes,
      dataFacultys,
      dataCourses,
      dataStudyFields,
      fromYear,
      toYear,
    } = this.state;
    return (
      <Card title={`${zoneId ? 'Cập nhật' : 'Thêm'} lớp`}>
        <Row>
          <Form onSubmit={this.handleSearch}>
            {/* <FilterPathEducate column="column" classes={{ isShow: false }} form={this.props.form} /> */}
            <Form.Item {...formItemLayout} style={{ display: 'none' }} label="Niên khóa">
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Hệ" hasFeedback>
              {getFieldDecorator('trainTypeId', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  {dataTrainTypes.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khoa" hasFeedback>
              {getFieldDecorator('facultyId', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  {dataFacultys.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khoá" hasFeedback>
              {getFieldDecorator('courseId', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select onChange={this.changeCourse}>
                  {dataCourses.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ năm" hasFeedback>
              {getFieldDecorator('fromYear', {
                initialValue: fromYear,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến năm" hasFeedback>
              {getFieldDecorator('toYear', {
                initialValue: toYear,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Chuyên ngành" hasFeedback>
              {getFieldDecorator('studyFieldId', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  {dataStudyFields.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên lớp" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số SV" hasFeedback>
              {getFieldDecorator('noOfStudent', {
                initialValue: initialValue.noOfStudent,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormClassesPage {...props} />
  </Page>
));



