import { request } from 'utils';

const prefix = '/api/categories';

export const getTrainTypes = ({
  label,
  labelInEnglish,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/TrainType`,
    method: 'GET',
    params: { label, labelInEnglish, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createTrainType = ({ id, label, labelInEnglish, regularId } = {}) =>
  request({
    prefix,
    url: `/TrainType`,
    method: 'POST',
    params: {},
    data: { id, label, labelInEnglish, regularId },
  });

export const getTrainType = ({ id } = {}) =>
  request({
    prefix,
    url: `/TrainType/GetById`,
    method: 'GET',
    params: { id },
    data: {},
  });

export const updateTrainType = ({ id, label, labelInEnglish, regularId } = {}) =>
  request({
    prefix,
    url: `/TrainType`,
    method: 'PUT',
    params: {},
    data: { id, label, labelInEnglish, regularId },
  });

export const deleteTrainType = ({ id } = {}) =>
  request({
    prefix,
    url: `/TrainType`,
    method: 'DELETE',
    params: {},
    data: { id },
  });



